.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.search-input-group {
  width: 100%;
}

.scroll-filter {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.nation-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.nation-flag {
  border: 1px solid blue;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.2rem;
}

.flag {
  height: 100%;
  box-shadow: rgba(14, 30, 37, 0.32) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}